    background: linear-gradient(to right, transparent 0%, rgba(254, 254, 254, 0.98) 98%, #fefefe 100%);
    height: 1px;
    opacity: 0.3;
    position: relative;
    top: 0;
    left: 0;
    width: 200px;
    -webkit-transition: opacity 0.8s, -webkit-transform 0.8s;
    transition: opacity 0.8s, -webkit-transform 0.8s;
    transition: opacity 0.8s, transform 0.8s;
    transition: opacity 0.8s, transform 0.8s, -webkit-transform 0.8s;
    -webkit-transform: rotate(45deg) translate(0);
            transform: rotate(45deg) translate(0);
    visibility: hidden;
}
body[data-mountain="earth"] .shooting-star.animate {
      opacity: 0;
      -webkit-transform: rotate(45deg) translate(50vw);
              transform: rotate(45deg) translate(50vw);
      visibility: visible;
}
body[data-mountain="earth"] .satellite-wrapper {
    -webkit-transform: perspective(1000px) rotateY(55deg);
            transform: perspective(1000px) rotateY(55deg);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: fixed;
    height: 80vh;
    top: 0;
    left: 0;
    width: 100vw;
}
@-webkit-keyframes satellite-path {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes satellite-path {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes satellite-blink {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes satellite-blink {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
body[data-mountain="earth"] .earth-satellite {
    -webkit-animation: satellite-path 30s linear infinite;
            animation: satellite-path 30s linear infinite;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
    background-color: rgba(255, 255, 255, 0.8);
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    height: 1px;
    width: 1px;
}
body[data-mountain="earth"] .earth-satellite:after {
      -webkit-animation: satellite-blink 1200ms infinite;
              animation: satellite-blink 1200ms infinite;
      background-color: red;
      display: block;
      content: '';
      width: 1px;
      height: 1px;
      -webkit-transform: translateY(1px);
              transform: translateY(1px);
}
body[data-mountain="earth"] .earth-satellite-1 {
